@charset "utf-8";

@font-face {
  font-family: "UTM-Sloop-1";
  src: url(../fonts/UTM-Sloop-1.ttf)format("truetype");
  font-weight: 400;
  font-style: italic
}

@font-face {
  font-family: "UTM-Cafeta";
  src: url(../fonts/UTM-Cafeta.ttf)format("truetype");
  font-weight: 400;
  font-style: italic
}

@font-face {
  font-family: "Ephesis-Regular";
  src: url(../fonts/Ephesis-Regular.ttf)format("truetype");
  font-weight: 400;
  font-style: italic
}

@font-face {
  font-family: "SVN-Mightiest";
  src: url(../fonts/SVN-Mightiest.otf)format("truetype");
  font-weight: 400;
  font-style: italic
}

@font-face {
  font-family: "UTM-Azkia";
  src: url(../fonts/UTM-Azkia.ttf)format("truetype");
  font-weight: 400;
  font-style: italic
}

@font-face {
  font-family: "Arial";
  src: url(../fonts/Arial.ttf) url(../fonts/Arialn.ttf) format("truetype");
  font-weight: 400;
  font-style: italic
}

@font-face {
  font-family: "Black Mango";
  src: url(../fonts/Black-Mango-Medium.ttf) format("truetype");
  font-weight: 400;
  font-style: italic
}



$font-utm-sloop: "UTM-Sloop-1",
sans-serif;
$font-utm-cafeta: "UTM-Cafeta",
sans-serif;
$font-ephesis: "Ephesis-Regular",
sans-serif;
$font-utm-azkia: "UTM-Azkia",
sans-serif;
$font-arial: "Arial",
sans-serif;
$font-svn-mightiest: "SVN-Mightiest",
sans-serif;
$font-black-mango: "Black Mango",
sans-serif;

$viewport-width: 1280px;
$wrapper-width: 980px;
$font-default: "Roboto",
sans-serif;
$font-DIN: "din-2014",
sans-serif;
$fontsize-default: 0.16rem;
$fontsize-title: 0.15rem;
$fontweight-light: 300;
$fontweight-default: 400;
$fontweight-medium: 500;
$fontweight-semibold: 600;
$fontweight-bold: 700;
$lineheight-default: calc(28.8 / 16);
$color-default: #333;
$color-light: #fff;
$color-dark: #000;
$color-background-primary: rgb(2, 121, 96) linear-gradient(0deg, rgba(2, 121, 96, 0) 0%, rgba(0, 166, 132, 1) 100%);
$color-background-secondary: rgb(0, 166, 132) linear-gradient(180deg, rgba(0, 166, 132, 1) 0%, rgba(0, 121, 96, 1) 100%);
