@charset "utf-8";
@use "_base/base"as *;

.main-common {
  --before-transform: translateX(0);
  --after-transform: translateX(0);

  position: relative;
  overflow: hidden;
  height: 100vh;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 0;
    display: block;
    width: 100%;
    height: 100dvh;
    z-index: 10;
    pointer-events: none;
    transition: transform 2s ease;
  }

  &::before {
    transform: var(--before-transform);
    background: url(../img/top/icon_03.webp) center center / cover no-repeat;
  }

  &::after {
    transform: var(--after-transform);
    background: url(../img/top/icon_04.webp) center center / cover no-repeat;
  }

  &.is-animation {
    &::before {
      animation: pulseCardLeft 3s infinite ease-in-out;
    }

    &::after {
      animation: pulseCardRight 3s infinite ease-in-out;
    }
  }

  &.is-active {
    height: auto;

    &::before {
      transform: translateX(100%);
    }

    &::after {
      transform: translateX(-100%);
    }
  }

  max-width: 4.2rem;
  margin: 0 auto;

  .main-button {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100dvh;
    z-index: 999;
  }
}

@keyframes pulseCardLeft {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(5%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes pulseCardRight {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(-5%);
  }

  100% {
    transform: translateX(0);
  }
}

.section-main {
  background: url(../img/top/bg_01.webp) top center / 100% 100% no-repeat;
  text-align: center;
  padding: 0.4rem 0;
  min-height: 100dvh;

  .main-title {
    text-transform: uppercase;
    -webkit-text-stroke: 0px rgba(0, 0, 0, 1.000);
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    color: rgba(0, 0, 0, 1);
    font-family: "Roboto", sans-serif;
    font-size: 0.2rem;
    letter-spacing: 0.03rem;
    line-height: 1.5;
    text-align: center;
  }

  .main-name {
    margin: 0 auto;
    max-width: 3.34rem;
  }

  .main-people {
    margin: 0 auto;
    max-width: 3rem;
  }

  .main-time {
    position: relative;

    &::before,
    &::after {
      position: absolute;
      content: "";
      background: url(../img/top/decor_02.webp) center center / contain no-repeat;
      display: block;
      width: 0.87rem;
      height: 0.87rem;
      top: 0.6rem;
    }

    &::before {
      left: 0;
    }

    &::after {
      right: 0;
    }

    &>span {
      display: block;
      position: relative;
    }
  }

  .time-date {
    -webkit-text-stroke: 0px rgba(0, 0, 0, 1.000);
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    color: rgba(0, 0, 0, 1);
    font-size: 0.15rem;
    line-height: 1.5;
    text-align: center;
    padding-top: 1rem;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      background: url(../img/top/decor_01.webp) center center / contain no-repeat;
      display: block;
      border-radius: 50%;
      width: 0.87rem;
      height: 0.87rem;
    }
  }

  .time-day {
    -webkit-text-stroke: 0 rgba(0, 0, 0, 1.000);
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    color: rgba(0, 0, 0, 1);
    font-size: 0.22rem;
    font-weight: bold;
    letter-spacing: 0.07rem;
    line-height: 1.5;
    text-align: center;
  }

  .main-image {
    position: relative;
    margin: 0.2rem auto;
    max-width: 2.49rem;

    &::before,
    &::after {
      position: absolute;
      content: "";
      display: block;
    }

    &::before {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 5px solid rgba(142, 1, 1, 1);
      pointer-events: none;
    }

    &::after {
      background: url(../img/top/icon_02.webp) center center / contain no-repeat;
      width: 1.17rem;
      height: 1.17rem;
      right: -0.6rem;
      bottom: -0.6rem;
    }
  }

  .main-invite {
    -webkit-text-stroke: 0px rgba(0, 0, 0, 1.000);
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    color: rgba(0, 0, 0, 1);
    font-family: $font-svn-mightiest;
    font-size: 0.22rem;
    font-weight: 700;
    line-height: 1.5;
    text-align: center;
  }

  .invite-dot {
    margin: 0 auto;
    width: 100%;
    max-width: 2.36rem;
    border: 1px dotted transparent;
    border-image: repeating-linear-gradient(to right, #000 0, #000 1px, transparent 1px, transparent 2px) 1;
  }

  .main-icon {
    margin: 0 auto;
    max-width: 0.86rem;
  }

  .icon-second {
    margin: -0.2rem auto 0;
    max-width: 0.68rem;
  }

  .main-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: -0.4rem;
  }

  .main-inner {
    max-width: 1.5rem;
    width: 100%;
    flex: 0 0 auto;
  }

  .main-text {
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    font-family: $font-utm-cafeta;
    font-size: 0.16rem;
    text-align: center;
    margin: 0;
    text-transform: uppercase;
    line-height: 1.4;
    font-weight: 500;
  }

  .main-address {
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    font-size: 0.12rem;
    text-align: center;
    line-height: 1.5;
  }

  .main-notify {
    font-family: $font-black-mango;
  }

  .text-wrapper {
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    color: rgba(0, 0, 0, 1);
    font-family: $font-utm-sloop;
    font-size: 0.4rem;
    text-align: center;
    text-shadow: 0 0.04rem 0.04rem rgba(255, 255, 255, 1);
    margin: 0;
  }

  .text-and {
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    color: rgba(0, 0, 0, 1);
    font-family: $font-utm-azkia;
    font-size: 40px;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(255, 255, 255, 1);
    margin: -0.3rem 0;
    transform: rotate(5deg);
  }

  .main-second-image {
    max-width: 3.3rem;
    margin: 0 auto;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      display: block;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 5px solid rgba(142, 1, 1, 1);
      pointer-events: none;
    }
  }

  .main-br {
    display: block;
    width: 1.76rem;
    height: 1.76rem;
    margin: -1.7rem auto -0.6rem;
    flex: 0 0 auto;
    background: url(../img/top/icon_06.webp) center center / contain no-repeat;
  }

  .main-label {
    margin: 0 auto;
    text-align: center;
    margin-bottom: 0.4rem;

    &.is-invite {
      max-width: 1.95rem;
    }

    &.is-story {
      max-width: 0.93rem;
    }

    &.is-confirm {
      max-width: 1.86rem;
    }
  }

  .main-notify-title {
    border-color: rgba(229, 231, 235, 1);
    border-style: solid;
    font-size: 14px;
    letter-spacing: 1px;
    text-align: center;
  }

  .main-date-title {
    font-family: $font-arial;
    font-size: 0.14rem;
    font-style: italic;
    letter-spacing: 0.01rem;
    text-align: center;
    margin-bottom: 0.1rem;
  }

  .main-block {
    margin: 0 auto 0.4rem;
    max-width: 3.1rem;
    border: 2px solid rgba(142, 1, 1, 1);
    border-radius: 0.16rem;
    padding: 0.2rem;
  }

  .block-address {
    font-family: $font-arial;
    font-size: 0.14rem;
    letter-spacing: 0.01rem;
    text-align: center;
  }

  .block-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.4rem;
    height: 0.32rem;
    border-radius: 0.42rem;
    border-style: solid;
    box-shadow: 0 0.04rem 0.04rem rgba(0, 0, 0, 0.25);
    color: rgba(255, 255, 255, 1);
    font-family: $font-arial;
    font-size: 0.14rem;
    background: rgba(177, 0, 0, 1);
    text-decoration: none;
    margin: 0 auto;
    animation: bounce 4s infinite;
  }

  .story-text {
    padding: 0 0.2rem;
    margin: -0.3rem 0 0.4rem;
    font-family: $font-arial;
    font-size: 0.12rem;
    text-align: left;

    p {
      margin-bottom: 0.1rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .main-date-time {
    margin-bottom: -0.1rem;
    font-size: 0.3rem;
    font-family: $font-utm-cafeta;
  }

  form {
    background-color: rgba(177, 0, 0, 1);
    border-radius: 0.16rem;
    padding: 0.25rem;
    max-width: 3rem;
    margin: -0.1rem auto 0;

    button {
      margin: 0.15rem auto 0;
      background-color: #fff;
      color: #000;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 1.68rem;
      height: 0.43rem;
      font-weight: 700;
      font-size: 0.14rem;
      text-transform: uppercase;
      border-radius: 0.05rem;
      box-shadow: 0 0.04rem 0.04rem rgba(0, 0, 0, 0.25);
    }
  }

  input,
  select {
    background: #fff;
    color: rgba(177, 0, 0, 1);
    height: 0.41rem;
    width: 100%;
    border-radius: 0.1rem;
    font-size: 0.12rem;
    padding: 0.02rem 0.1rem 0;
    margin-bottom: 0.1rem;

    &:focus {
      outline: none;
    }

    &::placeholder {
      font-size: 0.12rem;
      color: rgba(177, 0, 0, 1);
    }
  }

  .button-modal {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(177, 0, 0, 1);
    color: #fff;
    font-weight: 700;
    font-size: 0.14rem;
    border-radius: 0.09rem;
    width: 1.94rem;
    height: 0.4rem;
    padding-top: 0.02rem;
    box-shadow: 0 0.04rem 0.04rem rgba(0, 0, 0, 0.25);
  }

  .modal-overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
  }

  /* Modal container */
  .modal {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.8);
    background: #fff;
    border-radius: 12px;
    padding: 20px;
    width: 400px;
    max-width: 90%;
    z-index: 1000;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    transition: all 0.4s ease;
  }

  .modal.show {
    display: block;
    transform: translate(-50%, -50%) scale(1);
  }

  .modal-overlay.show {
    display: block;
  }

  .modal-header {
    font-size: 20px;
    margin-bottom: 15px;
  }

  .modal-body {
    font-size: 16px;
    margin-bottom: 20px;
  }

  .modal-footer {
    text-align: right;
  }

  .modal-footer button {
    padding: 8px 16px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    background: #007BFF;
    color: #fff;
    font-size: 14px;
  }

  .modal-footer button.close-btn {
    background: #ccc;
    color: #000;
    margin-right: 10px;
  }

  .p-album {
    display: flex;
    align-items: center;

    span:first-child {
      width: 1.76rem;
      margin: 0 auto;
    }

    .main-br {
      margin: 0.04rem 0 0;
      width: 1.4rem;
      height: 1.4rem;
    }
  }
}

.list-image-wrapper {
  margin-bottom: 0.4rem;

  .list-wrapper {
    @include reset-list;
    margin: 0 -0.05rem;
    display: flex;
  }

  .list-item {
    width: calc(100% /3);
    padding: 0 0.05rem;

    &:nth-child(2) {
      transform: translateY(-0.3rem);
    }
  }
}

.list-date-wrapper {
  margin-bottom: 0.3rem;

  .list-wrapper {

    @include reset-list;
    display: flex;
    align-items: center;
  }

  .list-item {
    width: calc((100% / 3) - 0.15rem);

    &:nth-child(2) {
      width: calc((100% / 3) + 0.15rem);
      border-left: 1px solid #000;
      border-right: 1px solid #000;
    }

    p {
      margin: 0;
      font-family: $font-arial;
      font-size: 0.14rem;
      letter-spacing: 0.01rem;
      text-align: center;
    }

    span {
      display: block;
      font-family: $font-arial;
      font-size: 0.14rem;
      letter-spacing: 0.03rem;
      text-align: center;
      font-weight: 500;
    }

    .number {
      font-family: $font-arial;
      font-size: 0.35rem;
      font-weight: 700;
      letter-spacing: 0.03rem;
      text-align: center;
      margin: -0.1rem 0;
    }
  }
}

@keyframes bounce {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes flash {

  0%,
  50%,
  100% {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}

.list-date {
  padding: 0.1rem;
  margin-bottom: 0.3rem;

  .list-wrapper {
    border: 2px solid rgba(177, 0, 0, 1);
    width: 100%;
    @include reset-list;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0.05rem;
    border-radius: 0.04rem;
  }

  .list-item {
    height: 0.4rem;
    width: 0.4rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: $font-arial;
    font-size: 0.14rem;

    &.is-bold {
      font-weight: 700;
      font-size: 0.16rem;
    }

    &.is-current {
      position: relative;

      &::before {
        position: absolute;
        content: "";
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url(../img/top/icon_08.webp) center center / contain no-repeat;
        display: block;
        width: 0.4rem;
        height: 0.4rem;
        animation: flash 4s infinite;
      }
    }
  }
}

.grid {
  margin: -0.5rem auto 0.4rem;
}

.grid-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: -0.05rem;
}

.grid-item {
  width: calc(100% / 2);
  padding: 0.05rem;
  color: #fff;
  border-radius: 0.08rem;
}

.grid-item.full {
  width: 100%;
}

.block-top-story {
  .story-viewport {
    height: 2.36rem;
    background: url(../img/top/ab_25x35_04.jpg) center center / cover no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(30, 27, 25, 0.7);
      pointer-events: none;
    }
  }
}

.tab-content {
  display: none;
  opacity: 0;
}

.tab-content.is-active {
  display: block;
  opacity: 1;
}

.story-controller {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.btn-common {
  width: 3.02rem;
  height: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba($color: #e9e2d1, $alpha: .75);
  cursor: pointer;
  position: relative;
  font-family: $font-arial;

  .text {
    transition: all 0.4s ease;
    font-size: 0.16rem;
    line-height: 1.6;
    color: rgb(37, 38, 40);
    text-align: center;
  }

  &.is-next {
    margin-top: 0.2rem;
    background: rgba($color: #efcb84, $alpha: .75);
  }

  &:hover {
    .text {
      transform: scale(1.05);
    }
  }

  .icon {
    position: absolute;
    top: calc(50% - 0.07rem);
    right: 0.4rem;
    width: 0.17rem;
    height: 0.3rem;
    animation-name: fadeInDown;
    animation-delay: 1s;
    animation-duration: 2s;
    animation-iteration-count: infinite;
  }
}


@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-0.1rem);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.tab-contents {
  padding: 0.1rem;
  background-color: #f3efe9;
  text-align: left;

  .content-wrapper {
    border: 1px solid #000;
    padding: 0.1rem;
  }

  .content-paragraph {
    font-family: $font-arial;
    font-size: 0.12rem;
    line-height: 1.6;
    padding-top: 0.55rem;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      left: 50%;
      top: 0;
      transform: translateX(-50%);
      background: url(../img/top/decor_09.png) center center / contain no-repeat;
      display: block;
      width: 0.85rem;
      height: 0.46rem;
      transition: transform 0.4s ease;
    }

    &::after {
      position: absolute;
      content: "";
      display: block;
      top: 0;
      left: 0;
      background: url(../img/top/blockquote.svg) center center / contain no-repeat;
      width: 0.96rem;
      height: 0.8rem;
      z-index: 0;
    }

    &:hover {
      &::before {
        transform: translateX(-50%) scale(1.05);
      }
    }

    p {
      margin-bottom: 0.05rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}


.main-thankyou {
  margin-top: 0.3rem;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    pointer-events: none;
  }

  .thankyou {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1.5rem;
    background: rgba(255, 255, 255, 0.3);
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
  }

  .thankyou-img {
    max-width: 3.3rem;
    margin: 0 auto;
  }
}

#wc-music-icon.is-on {
  display: none;
}

#wc-music-icon.is-on.is-active {
  display: block;
}
